import ZHeader from '@/components/common/ZHeader.vue'
import CateMenu from './CateMenu.vue'
import CategoryItem from './CategoryItem.vue'
export default {
    name: 'category',
    components: {
        ZHeader,
        CateMenu,
        CategoryItem
    },
    data () {
        return {
            detail: {
                id: 1,
                name: '',
                labels: [
                    {id:1, name:''},
                    {id:1, name:''},
                    {id:1, name:''},
                    {id:1, name:''},
                    {id:1, name:''}
                ]
            }
        }
    }
}



